.product-card {
  justify-content: center;
  align-items: center;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;
  display: flex;
  flex-direction: column;
  width: 200px;
}

.product-card__image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.product-card__title {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #060b35;
  margin-bottom: 4px;
}

.product-card__code {
  width: 100%;
  height: 14px;
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
  color: #616070;
  margin-bottom: 16px;
}

.product-card__stars-rating {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  width: 100%;
}

.product-card__rating-text {
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
  color: #060b35;
}

.product-card__price {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 16px;
}

.product-card__price-text {
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #616070;
}

.product-card__price-value {
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: #060b35;
}

.product-card__button {
  display: flex;
  width: 100%;
}

.product-card__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  background: #00acdc;
  border-radius: 5px;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  text-decoration: none;
  color: #fff;
}

.product-card__link:hover {
  background: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
  color: #00acdc;
}
