.stars {
  display: flex;
}

.stars__star {
  background-image: url(../../images/star.svg);
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url(../../images/star-active.svg);
}
